.container {
  box-sizing: border-box;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

@media (min-width: 992px) {
  .container {
    max-width: 962px; } }

@media (min-width: 1400px) {
  .container {
    max-width: 1140px; } }

.page-header {
  min-height: 735px;
  padding-top: 10px;
  background-color: #049acf; }

.page-header__container {
  padding: 0; }

.page-header__logo {
  display: block;
  padding: 0 15px 10px; }

.page-header__logo-image {
  display: block; }

.page-header__contacts {
  display: none; }

@media (min-width: 992px) {
  .page-header {
    padding-top: 15px; }
  .page-header__container {
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    flex-direction: column; }
  .page-header__wrap {
    display: flex;
    justify-content: space-between; }
  .page-header__context-wrap {
    display: flex;
    flex-direction: column; }
  .page-header__contacts {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 15px; } }

.main-nav {
  background-color: #fbd341; }

.main-nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  list-style: none; }

.main-nav__item {
  margin: 0;
  padding: 0;
  padding: 10px 15px;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  cursor: pointer; }
  .main-nav__item:last-child {
    margin-bottom: 0;
    border-bottom: none; }

.main-nav__item > a {
  font-size: 18px;
  font-weight: 700;
  color: #000000;
  text-decoration: none;
  line-break: auto; }

@media (min-width: 992px) {
  .main-nav {
    border-radius: 10px; }
  .main-nav__list {
    flex-direction: row; }
  .main-nav__item {
    position: relative;
    border: none; }
  .main-nav__sub-menu {
    content: "";
    position: absolute;
    top: 0;
    left: 0; } }

.contacts-text {
  margin-right: 40px;
  font-size: 14px;
  font-weight: 700;
  color: white; }
  .contacts-text:last-of-type {
    margin-right: 60px; }

.social-list {
  display: flex;
  list-style: none; }

.social-item {
  margin-right: 24px; }
  .social-item:last-child {
    margin-right: 0; }

.social-facebook {
  display: block;
  position: relative;
  width: 22px;
  height: 22px; }
  .social-facebook::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("./../../img/icon-facebook.png");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 100%; }

.social-instagram {
  display: block;
  position: relative;
  width: 22px;
  height: 22px; }
  .social-instagram::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("./../../img/icon-instagram.png");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 100%; }

.sub-menu__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.sub-menu__item {
  padding: 5px 0 5px 10px; }

.sub-menu__item > a {
  font-size: 14px;
  color: #000000;
  text-decoration: none; }

.advantages {
  background-color: #f8f7f7; }

.advantages__wrapper {
  display: flex; }

.advantages__title {
  margin: 0;
  padding: 0; }

.advantages__picture {
  width: 458px;
  height: 316px; }

.advantage__image {
  display: block; }

.page-footer__down {
  color: white;
  background-color: #22334d; }

.page-footer__down-wrap {
  display: flex; }

body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  color: #000000;
  background-color: white;
  overflow-x: hidden; }

img {
  max-width: 100%;
  height: auto; }

p, ul {
  margin: 0;
  padding: 0; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }
